export default {
	data() {
		return {
			customTheme:'',
			form: {

			}
		}
	},
	onLoad(e) {
		console.log(e,2333)
		this.customTheme = this.$store.state.customTheme
		this.getItemDetail(e.no)
	},
	methods: {
		getItemDetail(no) {
			this.$http({
				url: "/order/" + no,
				v3: true
			}).then(res=>{
				this.form = res
			})
		},
		itemJump(index, id) {
			if (this.type == 2) {
				uni.navigateTo({
					url: `/pages/home/item?id=${id}&order_id=${this.list[index].order_id}`,
					animationType: 'pop-in',
					animationDuration: 200
				});
			} else {
				uni.navigateTo({
					url: `/pages/home/item?id=${id}`,
					animationType: 'pop-in',
					animationDuration: 200
				});
			}
		},
		refund(e, index) {
			this.$http({
				url: '/order/' + e + '/refund',
				method: 'PUT',
				loading: 'none'
			}).then(res => {
				this.list[index].status = -2
				uni.showToast({
					title: '已申请退款',
					duration: 2000,
				});
			})
		}
	}
}
